import { useState, useEffect } from "react";
import { ENDPOINTS } from "@src/constants/endpoints";
import { HOST } from "@src/constants/url";
export default function useWaitingApprovalList() {
  const [dataRaw, setDataRaw] = useState([]);
  let token = JSON.parse(window.localStorage.getItem("accessToken"));
  const axios = require("axios");
  function getList() {
    axios
      .post(
        `${HOST}${ENDPOINTS.RES_WAITING_APPROVAL}`,
        {
          filter: {},
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      )
      .then(function (response) {
        setDataRaw([...dataRaw, ...response.data.data.data]);
      })        
  }
  useEffect(() => {
    getList();
  }, []);

  return { dataRaw, setDataRaw };
}
