import { notification } from 'antd';
import useWaitingApprovalList from '@src/services/getWaitingApprovalList';
import { HOST } from '@src/constants/url';
import { ENDPOINTS } from '@src/constants/endpoints';
import roles from '@src/constants/roles';

export default function DataProcess(role) {
  const axios = require('axios');
  //GET DATA RAW
  const { dataRaw, setDataRaw } = useWaitingApprovalList();
  const actionSubmit = async (isApproved, statusRes, index, ...args) => {
    const endpoint = isApproved
      ? statusRes === 4
        ? ENDPOINTS.APPROVAL_CLEAN_LIST
        : ENDPOINTS.APPROVAL_VERIFY_LIST
      : statusRes === 4
      ? ENDPOINTS.REJECT_CLEAN_LIST
      : ENDPOINTS.REJECT_VERIFY_LIST;
    let token = JSON.parse(window.localStorage.getItem('accessToken'));
    const [reason] = [...args];
    const defaultField = isApproved
      ? {
          approvalList: index,
        }
      : {
          rejectList: index,
          note: reason,
        };
    const header = {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    };
    await axios
      .post(`${HOST}${endpoint}`, defaultField, header)
      .then((response) => {
        notification.success({
          message: `${
            isApproved ? 'Approved' : 'Rejected'
          } ${index} Successfully!`,
          placement: 'topRight',
        });
        window.location.reload();
      })
      .catch((err) => {
        notification.error({
          message: `${isApproved ? 'Approved' : 'Rejected'} ${index} Error!`,
          placement: 'topRight',
        });
      });
  };
  //HANDLE CHANGE STATUS OF LIST ITEMS
  function changeDataRaw(key, index, statusRes, ...args) {
    const [reason] = [...args];
    const approvalCleanedIndex = [];
    const approvalVerifiedIndex = [];
    const rejectCleanedIndex = [];
    const rejectVerifiedIndex = [];
    //approveStatus
    dataRaw.length !== 0 &&
      dataRaw.map((obj) => {
        if (index.includes(obj.realEstateRawId)) {
          if (key === 1) {
            obj.approveStatus === 4
              ? approvalCleanedIndex.push(obj.realEstateRawId)
              : approvalVerifiedIndex.push(obj.realEstateRawId);
          } else {
            obj.approveStatus === 4
              ? rejectCleanedIndex.push(obj.realEstateRawId)
              : rejectVerifiedIndex.push(obj.realEstateRawId);
            notification.success({
              message: `Reject ${obj.realEstateRawId} Successfully!`,
              placement: 'topRight',
            });
          }
        }
      });
    approvalCleanedIndex.length !== 0 &&
      actionSubmit(true, 4, approvalCleanedIndex);
    approvalVerifiedIndex.length !== 0 &&
      actionSubmit(true, 8, approvalVerifiedIndex);
    rejectCleanedIndex.length !== 0 &&
      actionSubmit(false, 4, rejectCleanedIndex, reason);
    rejectVerifiedIndex.length !== 0 &&
      actionSubmit(false, 8, rejectVerifiedIndex, reason);
  }
  const sendMessage = (index) => {
    index.length !== 0 &&
      notification.success({
        message: `Sent message about ${index} Successfully!`,
        placement: 'topRight',
      });
  };
  const optionFunction = {
    changeDataRaw,
    sendMessage,
  };
  const formatData = (data) => ({
    id: (
      <div>
        <span>
          {role === roles.ADMIN ? <a href={`/view/${data.realEstateRawId}`}>#{data.realEstateRawId}</a> : ''} 
          {role !== roles.ADMIN ? <a href={`/detail/${data.realEstateRawId}`}>#{data.realEstateRawId}</a> : ''}
        </span>
      </div>
    ),
    address: (
      <div>
        <span>
          {data.areaWardName &&
            `${data.realEstateLocationHomeNumber} ${data.areaWardName}, ${data.areaDistrictName}
        , ${data.areaProvinceName}`}{' '}
        </span>
      </div>
    ),
    land: (
      <div className='double-cell'>
        <div>
          <span>Rng {data.realEstateLandWidth ?? 0} m</span>
          <br />
          <span>Di {data.realEstateLandLongs ?? 0} m</span>
          <br />
          <span>DT t {data.realEstateLandDefaultSquare ?? 0} m2</span>
          <br />
          <span>DT t ng {data.realEstateLandRoadSquare ?? 0} m2</span>
        </div>
        <div>
          <span>DTQH {data.realEstateLandRealConstructionSquare ?? 0} m2</span>
          <br />
          <span>DTTT {data.realEstateLandRealitySquare ?? 0} m2</span>
          <br />
          <span>{data.realEstateLandShapeName}</span>
        </div>
      </div>
    ),
    house: (
      <div>
        <span>{data.realEstateHouseFloors ?? 0} Tng </span>
        <br />
        <span>
          {data.realEstateHouseBedRooms ?? 0} PN,{' '}
          {data.realEstateHouseToilets ?? 0} WC
        </span>
        <br />
        <span>{data.realEstateHouseFurnitureList}</span>
      </div>
    ),
    juridical: (
      <div>
        <span>{data.realEstateJuridicalName} </span>
        <br />
        <span>Gi bn {data.realEstateValueSalePrice ?? 0}</span>
        <br />
        <span>Gi t {data.realEstateUnitPrice ?? 0}</span>
      </div>
    ),
    media: (
      <div>
        <span>
          {data.realEstateNumVideos ?? 0}{' '}
          {data.realEstateNumVideos > 1 ? 'videos' : 'video'}
        </span>
        <br />
        <span>{data.realEstateNumPics ?? 0} nh thc t</span>
        <br />
        <span>{data.realEstateDesign}</span>
      </div>
    ),
    score: (
      <div>
        <span>im thu thp: {data.realEstateCollectScore ?? 0}</span>
        <br />
        <span>im u T: {data.realEstateInvestScore ?? 0}</span>
        <br />
        <span>im nh gi: {data.realEstateFeedbackScore ?? 0}</span>
        <br />
        <span> im tng kt: {data.realEstateTotalScore ?? 0}</span>
      </div>
    ),
    rawID: data.realEstateRawId,
    status: data.approveStatus,
  });
  //FILL DATA FROM RAW TO DISPLAY
  const dataFill = [];
  dataRaw.map((obj) => {
    const temp = {
      ...formatData(obj),
      key: obj.realEstateRawId,
    };
    dataFill.push(temp);
  });
  return { dataFill, optionFunction };
}
