import React from "react";
import { Checkbox, Col, Row } from "antd";

export default function ColumnsFilter({ headerTable, setHeaderTable }) {
  function displayColumnHandle(value) {
    const newStatus = headerTable.map((obj) => ({ ...obj, isShowed: false }));
    value.map((obj) => {
      newStatus[+obj].isShowed = true;
    });
    setHeaderTable(newStatus);
  }

  return (
    <div
      className="dropdown-custom"
      style={{
        width: "260px",
        height: "190px",
      }}
    >
      <Row justify="center" className="dropdown-custom-title">
        Hin th Ct
      </Row>
      <Row gutter={[6, 6]} className="dropdown-custom-content">
        <Checkbox.Group
          onChange={displayColumnHandle}
          defaultValue={[0, 1, 2, 3, 4, 5, 6]}
        >
          <Row>
            {headerTable.map(
              (ele, index) =>
                index !== 3 && (
                  <Col span={12}>
                    <Checkbox value={index}>{ele.title}</Checkbox>
                  </Col>
                )
            )}
          </Row>
        </Checkbox.Group>
      </Row>
    </div>
  );
}
