import { Menu, Input, Modal, Dropdown, Button,notification } from "antd";
import { useRef } from "react";

import { ReactComponent as AcceptIcon } from "@src/assets/images/icons/file-icons/accept.svg";
import { ReactComponent as RejectIcon } from "@src/assets/images/icons/file-icons/reject.svg";
import { ReactComponent as RemindIcon } from "@src/assets/images/icons/file-icons/remind.svg";
import { ReactComponent as CancelIcon } from "@src/assets/images/icons/file-icons/Close.svg";
import { ReactComponent as ConfirmIcon } from "@src/assets/images/icons/file-icons/confirm.svg";
import { ReactComponent as ThreeDotVertical } from "@src/assets/images/icons/file-icons/three-dot-ver.svg";

const { TextArea } = Input;
function openNotification(placement, message) {
  notification.error({
    message: `${message}`,
    placement,
  });
};
export default function DropdownAccess(role) {
  const refReason = useRef("");
  const modalControl = (e, func, index,statusRes) => {
    const type = +e.key === 1;
    Modal.confirm({
      icon: type ? <ConfirmIcon /> : <CancelIcon />,
      title: type ? acceptMsg : rejectMsg,
      content: !type && (
        <div className="approval-modal">
          <p><i>{msg}</i></p>
          <TextArea
            allowClear
            showCount
            maxLength={255}
            onChange={(e) => {refReason.current = e.target.value.trim();}}
            autoSize={{ minRows: 3, maxRows: 10 }}
          />
        </div>
      ),
      onOk() {
        if (refReason.current||+e.key===1) {
          func(+e.key, index, statusRes,refReason.current);
          refReason.current = "";
        } else
          openNotification('topRight', "Please enter the reject reason.")

      },
      cancelText: "HY",
      className: `approval-modal ${type ? "accept-modal" : "reject-modal"}`,
      okText: type ? "XC NHN" : "T CHI",
    });
  };

  const menu = (func, index,statusRes) => {
    const menu_admin = (
      <div>
        <Menu onClick={(e) => modalControl(e, func, index,statusRes)}>
          <Menu.Item key="1" icon={<AcceptIcon />}>
            Chp nhn
          </Menu.Item>
          <Menu.Item key="2" icon={<RejectIcon />}>
            T chi/ Cnh bo
          </Menu.Item>
        </Menu>
      </div>
    );
    const menu_ic = (
      <Menu onClick={() => func(index)}>
        <Menu.Item key="1" icon={<RemindIcon />}>
          Gi li nhc
        </Menu.Item>
      </Menu>
    );
    return role === 4? menu_admin:( role === 2 || role === 3)? menu_ic: <></>;
  };

  function MultyAccess({ title, icon, func, indexes }) {
    func = role === 4 ? func.changeDataRaw : func.sendMessage;
    return (
      <Dropdown overlay={() => menu(func, indexes)} style={style}>
        <Button>
          {title || "Button"} &ensp;{icon}
        </Button>
      </Dropdown>
    );
  }

  function ThreeDotDrop({ func, id,statusRes }) {
    func = role === 4 ? func.changeDataRaw : func.sendMessage;
    return (
      <div>
        <Dropdown overlay={() => menu(func, [id],statusRes)}>
          <ThreeDotVertical />
        </Dropdown>
      </div>
    );
  }

  return {
    MultyAccess,
    ThreeDotDrop,
  };
}

const style = {
  borderRadius: "24px",
  background: "#F5F6FB ",
};
const msg = `Vui lng cho bit l do bn t chi duyt bt ng sn ny:`;
const acceptMsg = `Xc nhn duyt bt ng sn ny?`;
const rejectMsg = `T chi duyt bt ng sn ny?`;
