import React, { useState } from "react";
import { Table, Dropdown, Button } from "antd";
import { DownOutlined } from "@ant-design/icons";
import DropdownAccess from "./DropDown";
import DataProcess from "./DataProcess";
import { ColumnsFilter, DataFilter } from "./filters";

import { ReactComponent as HomeIcon } from "@src/assets/images/icons/file-icons/home.svg";
import { ReactComponent as ColumnsDisplayIcon } from "@src/assets/images/icons/file-icons/columns-display.svg";
import { ReactComponent as ChevronDownFill } from "@src/assets/images/icons/file-icons/chevrondown-fill.svg";
import { ReactComponent as FilterIcon } from "@src/assets/images/icons/filter.svg";
import 'antd/dist/antd.css';
import "./restable.scss";

export default function ResWaitingApproval() {
  const role = JSON.parse(window.localStorage.getItem("userData")).roleId;

  const { MultyAccess ,ThreeDotDrop} = DropdownAccess(role);
  //FILTER
  const [filter, setFilter] = useState({ isOpened: false });
  //GET DATA
  const { dataFill, optionFunction } = DataProcess(role);

  //SELECTED ITEMS
  const [selectedRowKeys, setSelectedRowKeys] = useState([]);
  const onSelectChange = (selectedRowKeys) => {
    setSelectedRowKeys(selectedRowKeys);
  };
  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectChange,
  };

  //DEFAULT COLUMNS
  const [headerTable, setHeaderTable] = useState([
    { title: "ID", dataIndex: "id", isShowed: true },
    { title: "a ch", dataIndex: "address", isShowed: true },
    { title: "Thng tin t", dataIndex: "land", isShowed: true },
    { title: "Thng tin nh", dataIndex: "house", isShowed: true },
    { title: "Php l", dataIndex: "juridical", isShowed: true },
    { title: "Media", dataIndex: "media", isShowed: true },
    { title: "im", dataIndex: "score", isShowed: true },
  ]);

  //COLUMNS DISPLAY
  const columns = [];
  headerTable.map((obj) => {
    obj.isShowed && columns.push(obj);
  });
  columns.push({
    title: "",
    dataIndex: "option",
  });
  dataFill&&dataFill.map(obj=>obj.option=(
      <>
      <ThreeDotDrop id={obj.rawID} func={optionFunction} statusRes={obj.status} />
      </>
  ))
  return (
    <div className="rstab">
      <div className="rstab__head">
        <div className="rstab__head_line">
          <div className="rstab__head_line_left">
            <span className="font-title font-w500">
              <HomeIcon />
              &ensp; BT NG SN CH XC NHN
            </span>
            <span className="font-highlight mg-17-53 font-italic font-w900">{`${dataFill.length} TOTAL`}</span>
          </div>
          <div className="rstab__head_line_right font-highlight font-w500">
            <Dropdown
              overlay={
                <ColumnsFilter
                  headerTable={headerTable}
                  setHeaderTable={setHeaderTable}
                />
              }
            >
              <Button>
                <ColumnsDisplayIcon /> Hin th ct{" "}
                <DownOutlined style={{ marginBottom: "-5px" }} />
              </Button>
            </Dropdown>
            <Button
              type="primary"
              onClick={() =>
                setFilter({ ...filter, isOpened: true })
              }
            >
              <FilterIcon />
              &ensp; B lc (2)
            </Button>
            <DataFilter filter={filter} setFilter={setFilter} />
          </div>
        </div>
        <div className="rstab__head_line">
          <div className="font-secondary font-italic font-w500">
             chn {selectedRowKeys.length} BS &ensp;
            <MultyAccess
              func={optionFunction}
              indexes={selectedRowKeys}
              title="Chn thao tc"
              icon={<ChevronDownFill />}
            />
          </div>

        </div>
      </div>
      <Table
        style={{ borderRadius: "24px" }}
        rowSelection={rowSelection}
        pagination={{ pageSize: 20 }}
        columns={columns}
        dataSource={dataFill}
      />
    </div>
  );
}
